@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Unbounded:wght@200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url(/PogadaApp/static/media/thunderstorm.1c75d049.jpg), url(/PogadaApp/static/media/rain.751a1f82.jpg);
  background-position: center 0px;
  background-size: 2200px;
  background-repeat: no-repeat;
  transition: 2.5s;
}

body.drizzle {
  background-image: url(/PogadaApp/static/media/drizzle.0ca0231c.jpg);
  transition: 2.5s;
}

body.rain {
  background-image: url(/PogadaApp/static/media/rain.751a1f82.jpg);
  transition: 2.5s;
}

body.snow {
  background-image: url(/PogadaApp/static/media/snow.17ae114d.jpg);
  transition: 2.5s;
}

body.rain {
  background-image: url(/PogadaApp/static/media/rain.751a1f82.jpg);
  transition: 2.5s;
}

body.thunderstorm {
  background-image: url(/PogadaApp/static/media/thunderstorm.1c75d049.jpg);
  transition: 2.5s;
}

body.mist,
body.smoke,
body.haze,
body.fog {
  background-image: url(/PogadaApp/static/media/fog.ef7c8c8b.jpg);
  transition: 2.5s;
}

body.dust,
body.sand,
body.ash {
  background-image: url(/PogadaApp/static/media/dust.e08120d2.jpg);
  transition: 2.5s;
}

body.squall {
  background-image: url(/PogadaApp/static/media/squall.70aac00f.jpg);
  transition: 2.5s;
}

body.tornado {
  background-image: url(/PogadaApp/static/media/tornado.7f0e756f.jpg);
  transition: 2.5s;
}

body.clear {
  background-image: url(/PogadaApp/static/media/clear.ad8afa67.jpg);
  transition: 2.5s;
}

body.clouds {
  background-image: url(/PogadaApp/static/media/clouds.eb8311eb.jpg);
  transition: 2.5s;
}

a {
  text-decoration: none;
  color: rgb(215, 200, 255);
}

a:hover {
  text-decoration: underline;
}

#root {
  width: 100vw;
  height: 100vh;
}

.App {
  background-color: #8b8a8a89;
  width: 400px;
  min-height: 600px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: rgb(250, 235, 235);
  text-shadow: 2px 2px black;
  transition: 0.5s;
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
}

.App.inactive {
  min-height: 100px;
  height: 100px;
  transition: 0.5s;
}

.App__search-component {
  margin-top: 30px;
}

.App__input input {
  margin-right: 20px;
  width: 200px;
  height: 40px;
  font-size: 20px;
  border-radius: 7px;
  padding: 5px;
}

.App__button {
  width: 100px;
  height: 40px;
  font-size: 20px;
  border-style: none;
  border-radius: 7px;
  cursor: pointer;
  background-color: rgb(231, 231, 231);
  transition: 0.3s;
}

.App__button:hover {
  background-color: rgb(141, 141, 141);
}

.App__button:active {
  color: rgb(87, 87, 87);
}

.App__more-component {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 140px;
  width: 60px;
  position: absolute;
  top: 50%;
  left: 330px;
  font-size: 30px;
  background-color: rgba(101, 102, 107, 0.363);
  border-radius: 30px;
  text-shadow: none;
  cursor: pointer;
  transition: 0.5s;
}

.App__more-component:hover {
  background-color: black;
  width: 200px;
  left: 330px;
}

.App__more-component.active {
  background-color: rgba(0, 0, 0, 0.747);
  left: -200px;
  top: 120px;
  width: 800px;
  height: 470px;
  flex-direction: column;
  justify-content: flex-start;
}

.App__more-component__title {
  font-weight: bold;
  text-shadow: 2px 2px rgb(88, 88, 88);
  margin-top: 15px;
}

.App__more-component__hours {
  display: flex;
  width: 800px;
  margin-top: 15px;
}

.App__more-component__hour {
  display: flex;
  flex-direction: column;
  width: 100px;
  font-size: 18px;
  border-right: solid 1px white;
}

.App__more-component__hour:nth-of-type(8) {
  border-right: none;
}

.App__more-component__hour div {
  height: 40px;
}

.App__more-component__hour div.hour {
  font-size: 25px;
}

.App__more-component__hour div.temp {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App__more-component__hour div.temp img {
  width: 18px;
  height: 18px;
  position: relative;
  top: 1px;
}

.App__more-component__hour div.weather {
  font-size: 13px;
  height: 80px
}

.App__more-component__hour div.weather img {
  width: 70px;
  height: 70px;
}

.App__more-component__hour div.clouds,
.App__more-component__hour div.wind,
.App__more-component__hour div.pressure,
.App__more-component__hour div.humidity {
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App__more-component__hour div.clouds img,
.App__more-component__hour div.wind img,
.App__more-component__hour div.pressure img,
.App__more-component__hour div.humidity img {
  height: 18px;
  width: 24px;
  padding-right: 6px;
}

.App__more-component__hour div.wind div {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 4px;
}

.App__more-component__footer {
  margin-top: 5px;
  font-size: 17px;
}

.App__view {
  margin-top: 10px;
  min-width: 320px;
  transition: 1.5s;
}

.App__view-time {
  font-size: 15px;
}

.App__view-title {
  margin: 30px auto 0;
  min-width: 200px;
  padding: 10px;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(31, 31, 31, 0.363);
  border-radius: 10px;
}

.App__view-title h1 {
  letter-spacing: 2px;
  line-height: 43px;
}

.App__view-data {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
}

.App__view-data-general {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 30px;
}

.App__view-data-general div:nth-of-type(2) div {
  font-size: 16px;
}

.App__view-temp {
  font-size: 40px;
  font-weight: bold;
}

.App__view-sun {
  margin-top: 10px;
  display: flex;
  justify-content: space-around;
  font-size: 17px;
}

.App__view-sun img {
  width: 70px;
}

.App__view-details {
  margin: 10px 30px 0;
  background-color: rgba(31, 31, 31, 0.363);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  border-radius: 10px;
  padding: 0 15px 15px 15px;
  margin-bottom: 30px;
}

.App__view-pressure {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-pressure img {
  width: 30px;
  margin-right: 10px;
}

.App__view-windspeed {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-windspeed img {
  width: 30px;
  margin-right: 10px;
}

.App__view-clouds {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-clouds img {
  width: 30px;
  margin-right: 10px;
}

.App__view-humidity {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-humidity img {
  width: 30px;
  margin-right: 10px;
}
